import React from 'react';
import { GalleryItemObject } from '../GalleryPage/GalleryPage.types';
import styles from './GalleryItem.module.scss';

interface Props {
    item: GalleryItemObject;
    size?: 'sm' | 'lg';
    onClick?: (item: GalleryItemObject) => void;
}

const GalleryItem = ({ item, size, onClick }: Props): JSX.Element => {
    return (
        <div className={styles.galleryItem}>
            <div
                className={styles.imageOverlay}
                role="button"
                onClick={() => {
                    onClick(item);
                }}
            >
                <h2
                    className={
                        size === 'sm' ? styles.itemTitleSmall : styles.itemTitle
                    }
                >
                    {item.title}
                </h2>
                <span
                    className={
                        size === 'sm'
                            ? styles.itemAuthorSmall
                            : styles.itemAuthor
                    }
                >
                    {item.author}
                </span>
            </div>
            <img src={item.img} className={styles.imageThumbnail} />
        </div>
    );
};

export default GalleryItem;
