import { Container, Grid } from '@mui/material';
import React, { useState } from 'react';
import GalleryItem from '../GalleryItem/GalleryItem';
import GalleryModal from '../GalleryModal/GalleryModal';
import { MockGalleryItems } from './GalleryMockData';
import styles from './GalleryPage.module.scss';
import { GalleryItemObject } from './GalleryPage.types';

const GalleryPage = (): JSX.Element => {
    const [isItemOpened, setIsItemOpened] = useState(false);
    const [openItemed, setOpenedItem] = useState<GalleryItemObject>();

    const onCloseItem = () => {
        setIsItemOpened(false);
    };

    const onOpenItem = (item: GalleryItemObject) => {
        setIsItemOpened(true);
        setOpenedItem(item);
    };

    const featuredItems = MockGalleryItems.slice(0, 4).map((item) => {
        return (
            <Grid item xs={12} md={6} key={item.title}>
                <GalleryItem item={item} size="lg" onClick={onOpenItem} />
            </Grid>
        );
    });

    const otherItems = MockGalleryItems.slice(5, MockGalleryItems.length).map(
        (item) => {
            return (
                <Grid item xs={6} md={4} key={item.title}>
                    <GalleryItem item={item} size="sm" onClick={onOpenItem} />
                </Grid>
            );
        },
    );

    return (
        <div className={styles.background}>
            <GalleryModal
                item={openItemed}
                isOpen={isItemOpened}
                onClose={onCloseItem}
            ></GalleryModal>
            <Container maxWidth="lg" className={styles.container}>
                <h1 className={styles.title}>Gallery</h1>
                <Grid container spacing={4} className={styles.gridFeatured}>
                    {featuredItems}
                </Grid>
                <Grid container spacing={3} className={styles.gridAll}>
                    {otherItems}
                </Grid>
            </Container>
        </div>
    );
};

export default GalleryPage;
